@import url(https://fonts.googleapis.com/css2?family=Poppins:wght@500&display=swap);
.header{
    display: -webkit-flex;
    display: flex;
    -webkit-justify-content: center;
            justify-content: center;
    -webkit-align-items: center;
            align-items: center;
    width: 100%;
}
h1{
    color: white;   
    text-align: center;
}

.date{
    font-weight: lighter;
    font-size: 13px ;
    word-spacing: 4px;
}


.center{
    width: 100%;
    margin-bottom: 5rem;
    text-align: center;
}

.tab{
    
    color: #db5581 ;
    background-color: #DB558120;
    text-align: center;
    width: 50%;
}

.td{
    text-align: center;
}

.red{
    color:#ff073a;
}
.redp{
    color: rgba(255, 7, 58, 0.6);
    font-size: 13px;
    font-weight:900 ;
}

.blue{
    color: #007bff;
}

.green{
    color: #28a745 ;
}

.grey{
    color: #6c757d;
}

.num{
    font-size: 22px;
    font-weight: 600;
}
.plus{
    font-size: 13px;
    font-weight:900 ;
}
span{
    font-size: 12px;
    font-weight: 600;
}

.top{
    padding-bottom: 8px;    
}

.mid{
    padding-bottom: 1px;
}


.states{
    width: 100%;
    color: #6c757d;
    font-weight: 900;
}

th{
    text-align: center;
}

.td2{
    text-align: center;
}

#st{
    width: 20% ;
    text-align: left;
    padding-left: 13px ;
}

.states td,th{
    width: 20%;
    padding: 2px;
    border: 5px solid #161625;
    background-color: #1E1E30;
    border-radius: 8%;
}
.hover:hover{
    background-color: #282840;
}
.plusred{
    color: red;
    font-size:12px
}
.plusgreen{
    color: #28a745;
    font-size:12px
}
.plusgrey{
    color: #6C757D;
    font-size:12px
}
h4{
    text-align: center;
    color: #db5581 ;
    word-spacing: 3px;
}
.end{
    background-color: #db558120;
}
.container{
  -webkit-flex-direction: column;
          flex-direction: column;
  margin-left: 0;
  margin-right: 0;
  padding-top: 1rem;
  width: 98%;
  margin: 1%;
  font-family: 'Poppins', sans-serif;
}

body{
  background-color: #161625;
}
